html{
background-color: #82919c;
/*background: linear-gradient(180deg, #5d7181, #1a3346 50%);
*/}
.card{
	overflow: hidden;
	display: grid;
	grid-template-rows: 20% 60% 20%;
	margin: 30px; 
	background-color: #c8b98c;
	border-style: ridge;
	border-width: 1px;
	border-radius: 5px;
	border-color: #7b6c40;
	padding: 30px;
	margin-bottom: 10px;
	min-width: 434px;
	height: 70vh;
}
.headOfCard{
	display: flex;
	align-items: center;
	color: white;
	background-color: #a77662;
	padding-top: 0px;
	margin-top: 0px;
	grid-column-start: 0;
	grid-column-end: 1;

}

.card > p{
}
h3{
	padding: 0px;
	margin: 0px;
	background-color: #c89c8c;
}
.contentOfCard{
	grid-row-start: 4;
	grid-row-end: 5;
}
.video{
	width: 100%;
	max-width: 560px;
	grid-row-start: 2;
	grid-row-end: 3;
}
iframe{
	width: 100%;
	min-width: 200px;
	margin-top: 10px;
}